import { createGlobalStyle } from "styled-components"

const GlobalStyles = createGlobalStyle`

    // Variables

    :root {

        // Font families

        --family-heading: "DM Serif Display", sans-serif;
        --family-body: "Inter", sans-serif;

        // Line heights

        --line-height-tight: 1;
        --line-height-heading: 1.3;
        --line-height-paragraph: 1.9;
        --line-width: 1.5px;

        // Spacing units
        
        --su: 32px;
        --nav-height: 102px;

        @media (min-width: 800px) {
            --su: 54px;
            --nav-height: 206px;
        }

        @media (min-width: 1200px) {
            --su: 80px;
        }

        @media (min-width: 1600px) {
            --su: 120px;
        }

        // Element sizes

        --article-max-width: 800px;
        --site-max-width: 1600px;

    }

    // Reset and adjusting defaults

    *,
    *:after,
    *:before {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }

    img {
        display: block;
        width: 100%;
        max-width: 100%;
    }

    svg {
        display: block;
    }

    video {
        display: block;
        width: 100%;
    }

    // Base page styles

    body {
        -webkit-font-smoothing: antialiased;
        background-color: ${props => props.theme.background.normal};
        color: ${props => props.theme.text.normal};
        font-family: var(--family-body);
        font-weight: 400;
        line-height: var(--line-height-heading);
        
    }

    // Base typography styles
        
    h1 {
        font-family: var(--family-heading);
        font-size: 72px;
        font-weight: 400;
        line-height: var(--line-height-tight);

        @media screen and (min-width: 800px) {
            font-size: calc(71px + (121 - 71) * ((100vw - 800px) / (1200 - 800)));
        }

        @media screen and (min-width: 1200px) {
            font-size: calc(108px + (158 - 108) * ((100vw - 1200px) / (1600 - 1200)));
        }

        @media screen and (min-width: 1600px) {
            font-size: calc(138px + (258 - 138) * ((100vw - 1600px) / (2560 - 1600)));
        }

        @media screen and (min-width: 2560px) {
            font-size: 258px;
        }

    }

    h2 {
        font-family: var(--family-heading);
        font-size: 58px;
        font-weight: 400;
        line-height: var(--line-height-tight);

        @media (min-width: 800px) {
            font-size: 120px;
        }

    }

    h3 {
        font-family: var(--family-heading);
        font-size: 32px;
        font-weight: 400;
        line-height: var(--line-height-heading);

        @media (min-width: 800px) {
            font-size: 48px;
        }

    }

    h4 {
        font-family: var(--family-body);
        font-size: 20px;
        font-weight: 400;
        line-height: var(--line-height-heading);

        @media (min-width: 800px) {
            font-size: 24px;
        }

    }

    h5 {
        font-family: var(--family-body);
        font-size: 16px;
        font-weight: 400;
        line-height: var(--line-height-heading);

        @media (min-width: 800px) {
            font-size: 18px;
        }

    }

    h6 {
        font-family: var(--family-body);
        font-size: 15px;
        font-weight: 400;
        letter-spacing: 0.08em;
        line-height: var(--line-height-tight);
        text-transform: uppercase;
    }

    p {
        font-family: var(--family-body);
        font-size: 20px;
        font-weight: 400;
        line-height: var(--line-height-paragraph);

        @media (min-width: 800px) {
            font-size: 24px;
        }

    }

    blockquote {
        border-left: 1px solid ${props => props.theme.line.normal};
        padding: 8px 32px;
        font-size: 32px;
        line-height: var(--line-height-paragraph);
    }

    pre {
        background: ${props => props.theme.code.background};
        color: ${props => props.theme.code.text};
        padding: 24px;
        font-size: 13px;

        code {
            display: block;
            overflow-x: scroll;
        }

        @media (min-width: 800px) {
            font-size: 18px;
            padding: 32px;
        }

    }

    code {
        background: ${props => props.theme.code.background};
        color: ${props => props.theme.code.text};
        font-size: 13px;

        @media (min-width: 800px) {
            font-size: 18px;
        }


    }

    // Link chaos

    a,
    a:visited,
    button {
        background-image: linear-gradient(${props => props.theme.link.underline.normal} 0%, ${props => props.theme.link.underline.normal} 100%);
        background-position-y: 1.12em;
        background-repeat: repeat-x;
        background-size: 100% 1px;
        color: ${props => props.theme.link.text.normal};
        text-decoration: none;

        svg path {
            fill: ${props => props.theme.link.text.normal};
        }

        &:hover {
            background-image: linear-gradient(${props => props.theme.link.underline.hover} 0%, ${props => props.theme.link.underline.hover} 100%);
            color: ${props => props.theme.link.text.hover};

            svg path {
                fill: ${props => props.theme.link.text.hover};
            }

        }

        &.inverted {
            background-image: linear-gradient(${props => props.theme.link.underline.inverted} 0%, ${props => props.theme.link.underline.inverted} 100%);
            color: ${props => props.theme.link.text.inverted};

            svg path {
                fill: ${props => props.theme.link.text.inverted};
            }

            &:hover {
                background-image: linear-gradient(${props => props.theme.link.underline.invertedHover} 0%, ${props => props.theme.link.underline.invertedHover} 100%);
                color: ${props => props.theme.link.text.invertedHover};

                svg path {
                    fill: ${props => props.theme.link.text.invertedHover};
                }    

            }

        }

        &.hideUnderline {
            background: none;

            &:hover {
                background: none;
            }

        }

    }

    h2 a,
    h3 a {
        background-position-y: 1.19em;
        text-shadow:
            -1px 0 ${props => props.theme.background.normal},
            1px 0 ${props => props.theme.background.normal},
            -2px 0 ${props => props.theme.background.normal},
            2px 0 ${props => props.theme.background.normal},
            -3px 0 ${props => props.theme.background.normal},
            3px 0 ${props => props.theme.background.normal};

        &.inverted {
            text-shadow:
                -1px 0 ${props => props.theme.background.inverted},
                1px 0 ${props => props.theme.background.inverted},
                -2px 0 ${props => props.theme.background.inverted},
                2px 0 ${props => props.theme.background.inverted},
                -3px 0 ${props => props.theme.background.inverted},
                3px 0 ${props => props.theme.background.inverted};
        }

    }

    button {
        background-color: transparent;
        border: none;
        cursor: pointer;
        font: inherit;
        line-height: initial;
        margin: 0;
        padding: 0;
    }
    
    // Generic .has-shadow class to place a shadow on any element

    .has-shadow {
        box-shadow: 0 32px 32px rgba(0,0,0,0.05), 0 16px 16px rgba(0,0,0,0.05), 0 8px 8px rgba(0,0,0,0.05), 0 4px 4px rgba(0,0,0,0.05), 0 0 2px rgba(0,0,0,0.2);
    }

`
export default GlobalStyles