import styled from "styled-components"

export const Wrapper = styled.div`
    background-color: ${props => props.theme.background.inverted};
    border-top: var(--line-width) solid ${props => props.theme.line.normal};
    margin-top: 40px; // Accounts for headshot
    position: relative;
    z-index: 3;

    @media (min-width: 800px) {
        margin: 0;
    }

`

export const Headshot = styled.div`
    border-radius: 50%;
    height: 80px;
    margin-bottom: var(--su);
    margin-top: calc((var(--su) * -2) - 40px);
    overflow: hidden;
    user-select: none;
    width: 80px;

    @media (min-width: 800px) {
        margin-bottom: var(--su);
        margin-top: calc((var(--su) * -1) - 40px);
    }

    @media (min-width: 1200px) {
        margin-bottom: calc(var(--su) - 40px);
    }

`

export const Primary = styled.div`
    border-bottom: var(--line-width) solid ${props => props.theme.line.inverted};
    padding-bottom: calc((var(--su) * 2) - 16px); // Accounts for bottom margin on links

    @media (min-width: 800px) {
        padding-bottom: calc(var(--su) - 16px);
    }


    h6 {
        margin: var(--su) 0 16px;

        @media (min-width: 800px) {
            margin: 0 0 24px;
        }

    }

    p {
        margin-bottom: 16px;

        @media (min-width: 800px) {
            margin-bottom: 24px;
        }
        
    }

`

export const Secondary = styled.div`
    margin-top: calc(var(--su) * 2);

    @media (min-width: 800px) {
        margin-top: var(--su);
    }


`

export const LinkList = styled.div`
    display: flex;
    flex-wrap: wrap;

    a {
        display: inline-block;
        margin: 0 16px 16px 0;
    }

`

export const Footnote = styled.h5`
    color: ${props => props.theme.text.invertedMuted};
`